@import "./components";
@import "./styles";

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  position: relative;
  background-color: #282c34;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;

  overflow: hidden;
}

.App-link {
  color: #61dafb;
}

.profile {
  object-fit: cover;
  width: 100%;

  max-height: 600px;
  object-position: center;
}

.intro {
  position: absolute;
  top: 0;
  left: 0;

  margin: 0;
  background-color: rgba(0, 0, 0, 0.9);

  width: 100%;
  height: 100%;

  display: flex;
  align-items: center;
  justify-content: center;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

h2 {
  margin: 0;
}

.view-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  @include minWidth($bp-md) {
    flex-direction: row;
    align-items: flex-start;
    gap: 16px;

    height: 100%;
  }
  max-width: 1920px;

  margin: auto;
}

.view-container .MuiCard-root {
  margin: 16px 0;
}

.view {
  width: 100%;

  flex-direction: column;
  align-items: center;
  justify-content: center;

  gap: 16px;

  @include minWidth($bp-md) {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

.skill-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;

  gap: 16px;

  @include minWidth($bp-md) {
    > .MuiCardContent-root {
      display: grid;
      grid-template-columns: 1fr 1fr;

      gap: 16px;
    }
  }
}

.skill {
  display: flex;

  align-items: center;
  justify-content: space-between;
  gap: 8px;

  flex: 1 1 auto;
  flex-wrap: wrap;
  width: 100%;
}

.skill > * {
  max-width: 150px;
  height: fit-content;
}

a {
  color: inherit;
}

.MuiDivider-root {
  width: 100%;
}

.MuiTypography-body2 {
  padding-bottom: 16px;
}

ul li {
  line-height: 150%;
}
