@import "../../styles";
.SNBJSX {
  position: sticky;
  top: 0;

  height: 100vh;

  padding: 10px;

  display: flex;
  align-items: center;
  gap: 8px;

  @include minWidth($bp-md) {
    flex-direction: column;

    gap: 24px;
    align-items: flex-start;

    font-size: 36px;

    border-right: 1px solid black;

    a {
      text-decoration: none;
    }
  }
}
